import { h } from '@stencil/core';
import {
  MtEspecialidadesEnum,
  MtProdutosEnum,
} from '../../../../global/typescript/enums';

export const markdown = (
  produto?: string,
  especialidade?: string,
  _apostila?: string,
  _parte?: string,
  _ano?: string,
) => {
  if (produto === MtProdutosEnum.MEDCURSO) {
    if (especialidade === MtEspecialidadesEnum.NEF) {
      return (
        <div class="mt-titulo-h1__container">
          <p class="mt-titulo-h1__pre-title">
            <slot name="pre-title"></slot>
          </p>

          <h1 class="mt-titulo-h1__title">
            <slot name="title"></slot>
          </h1>

          <h2 class="mt-titulo-h1__subtitle">
            <slot name="subtitle"></slot>
          </h2>
        </div>
      );
    }
  }

  return (
    <h1>
      <slot></slot>
    </h1>
  );
};
